import { Button } from 'primereact/button';
import { InputText } from 'primereact/inputtext';
import { useState } from 'react';

const EmailBox = () => {
    const [value, setValue] = useState('')
    return <>
        <div className='p-grid' style={{ alignItems: 'center' }}>
            <div className="p-col-2">
                <img style={{ width: 32 }} alt="Card" src={`${process.env.PUBLIC_URL}/images/emailbox.svg`} />
            </div>
            <div className="p-col-6 p-0">
                <p className='font-weight-bold'>Email</p>
            </div>
        </div>
        <InputText className='w-100 mt-2' value={value} placeholder='Write on email...' onChange={(e) => setValue(e.target.value)} />
        <div className='pt-2 text-center'>
        <Button label="Compose" className="bg-base1 p-button-raised p-button-rounded" style={{width: '12rem'}} />
        </div>
    </>
}

export default EmailBox;